<template>
    <div>
      <section class="pc-container pb-4">
        <div class="pcoded-content">
          <div class="page-header">
                <div class="page-block">
                    <div class="row align-items-center">
                        <div class="col-md-12">
                            <div class="page-header-title">
                                <h5 class="m-b-10">Historial</h5>
                            </div>
                            <ul class="breadcrumb">
                                <li class="breadcrumb-item">
                                    <router-link to="/sisge">Inicio</router-link>
                                </li>
                                <li class="breadcrumb-item">Visualizacion</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
          <div class="row">
            <div class="col-xl-12">
              <div class="card shadow">
                <div class="card-header">
                  <div class="d-flex">
                    <h5>Visualización de Historial</h5>
                  </div>
                </div>
                <div class="card-body">
                  <div class="table-responsive">
                    <table class="table table-bordered" ref="historialTable">
                      <thead>
                        <tr>
                          <th>Expediente</th>
                          <th>Demandante</th>
                          <th>Demandado</th>
                          <th>Fecha</th>
                          <th>Medio de Comunicacion</th>
                          <th>Detalles</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(historial, index) in historiales" :key="index">
                          <td>
                            <router-link
                              :to="{ name: 'expediente/detalle', params: { id: historial.exp_id } }"
                            >{{ historial.exp_numero }}</router-link>
                          </td>
                          <td v-if="historial.procesal=='demandante'">{{ nombreCompleto(historial) }}</td>
                          <td>UNPRG</td>
                          <td v-if="historial.procesal=='demandado'">{{ nombreCompleto(historial) }}</td>
                          <td>{{ historial.his_fecha_hora }}</td>
                          <td>{{ historial.his_medio_comuniacion }}</td>
                          <td>{{ historial.his_detalle }}</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  </template>
  
  <script>
  import { mapActions } from "vuex";
  import feather from "feather-icons";
  import $ from "jquery";
  import "datatables.net-bs5";
  import "datatables.net-bs5/css/dataTables.bootstrap5.min.css";
  import "datatables.net-buttons-bs5";
  import "datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css";
  
  export default {
    name: "HistorialList",
    data() {
      return {
        historiales: [],
        dataTable: null
      };
    },
    mounted() {
      this.initializeFeatherIcons();
      this.listarHistoriales();
    },
    methods: {
      ...mapActions(["get", "post"]),
  
      async listarHistoriales() {
        try {
          const response = await this.getHistoriales();
  
          if (response.data) {
            console.log(response.data);
            this.historiales = response.data;
            this.initializeDataTable();
          }
        } catch (error) {
          console.error("Error al intentar obtener los datos", error);
        } 
      },
  
      async getHistoriales() {
        return this.get({
          url: this.$store.getters.get__url + "/history",
          token: this.$store.getters.get__token,
        });
      },
  
      initializeFeatherIcons() {
        feather.replace();
      },
  
      initializeDataTable() {
        if (this.dataTable) {
          this.dataTable.destroy();
        }
  
        this.$nextTick(() => {
          this.dataTable = $(this.$refs.historialTable).DataTable({
            language: {
              url: "//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json",
            },
          });
        });
      },
  
      nombreCompleto(historial) {
        if (historial.tipo_persona === "natural") {
          return `${historial.nat_nombres} ${historial.nat_apellido_paterno} ${historial.nat_apellido_materno}`;
        } else if (historial.tipo_persona === "juridica") {
          return `${historial.jur_razon_social}`;
        }
      },
    },
  };
  </script>
  
  <style>
  td {
    white-space: normal !important;
    word-wrap: break-word;
  }
  
  th,
  td {
    font-size: 13px !important;
  }
  
  .is-invalid {
    border-color: red;
  }
  </style>
  